@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@400&display=swap);
*, body {
  margin: 0;
  font-family: 'Exo 2', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --theme-button-text: #FFF;
    --light: rgb(80, 80, 80);
    --dark: #000;
}

h1 {
    font-size: 8vh;
}

h3 {
    font-size: 4vh;
}

.light {
    --theme-page-background: #FFF;
    --theme-page-text: #111;
    --theme-button-color: rgb(40, 0, 238);
    --theme-same: var(--light);
    --theme-diff: var(--dark);
}

.dark {
    --theme-page-background: rgb(24, 24, 24);
    --theme-page-text: #FFF;
    --theme-button-color: rgb(250, 87, 0);
    --theme-same: var(--dark);
    --theme-diff: var(--light);
    /* --theme-button-text: #111; */
}

/* @import url('https://fonts.googleapis.com/css2?family=Exo+2:wght@300;400&display=swap'); */

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;

  background: var(--theme-page-background);
  color: var(--theme-page-text);
}

.corner {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

button {
  padding: 0.75em;
  margin: 1.5em;
  border-radius: 10%;
  background-color: var(--theme-button-color);
  color: var(--theme-button-text);
  font-size: 1rem;
  border: 0 solid transparent;
}

button:hover {
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

button:focus {
  outline: none;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .App {
    /* transform: scale(1); */
    overflow: hidden;
  }
  .Header {
    margin: 2%;
    padding: 0.75em!important;
    align-items: center;
    justify-content: center;
    transform: scale(0.70);
    transform-origin: center;
  }

  .badges {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    transform: scale(1.5);
  }
}

