.Header {
    padding: 2em 4em;
}

.badges {
    display: flexbox;
    text-align: center;
    transform: scale(2);
    transform-origin: center;
}
.col {
    margin: .5em;
    justify-content: space-evenly;
}
a {
    margin: 0.2em;
}

/* TODO: 3dp animation.. move to own component, fix colors for dark mode */
.animation {
    margin: 2em;
}

.threedp {
    height: 300px;
    width: 400px;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.box {
    position: absolute;
    width: 200px;
    height: 200px;
    border: 12px solid var(--theme-diff);
    border-radius: 10%;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
    z-index: 100;
    transform: translate(-15px, 0px);
}

.spool {
    position: absolute;
    height: 125px;
    width: 30px;
    border-top: 5px solid  var(--theme-page-background);
    border-left: 5px solid var(--theme-diff);
    border-right: 5px solid var(--theme-diff);
    border-bottom: 5px solid  var(--theme-page-background);
    background-color: orangered;
    transform: translate(120px, 5px)
}

.inner {
    width: inherit;
    height: 15px;
    position: absolute;
    background-color: transparent;
    transform: translate(0, 25px);
}

.bars {
    width: 400px;
    height: 15px;
    position: absolute;
    background-color: transparent;
    border-top: 5px solid var(--theme-diff);
    border-bottom: 5px solid var(--theme-diff);
    border-left: 0px;
    border-right: 0px;
    /* animation: raise 8s steps(8, end) reverse; */
    animation: move linear 8000ms;
    animation-direction: reverse;
    offset-path: path("M 102 -158 V -148.4 H 116.4 Q 121.2 -143.6 116.4 -138.8 H 82.8 Q 78 -134 82.8 -129.2 H 154.8 Q 159.6 -124.4 154.8 -119.6 H 44.4 Q 39.6 -114.8 44.4 -110 H 154.8 Q 159.6 -105.2 154.8 -100.4 H 44.4 Q 39.6 -95.6 44.4 -90.8 H 154.8 Q 159.6 -86 154.8 -81.2 H 42");
    offset-rotate: 0deg;
    z-index: -100;
}

@keyframes raise {
    from {
        transform: translate(0, 25px);
    }
    to {
        transform: translate(0, 90px);
    }
}

.bed {
    width: 150px;
    position: absolute;
    border-top: 10px solid var(--theme-diff);
    border-bottom: 5px solid transparent;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    transform: translate(20px, 175px);

}

.hotend {
    width: 45px;
    height: 45px;
    background-color: rgb(36, 36, 36);
    position: absolute;
    /* transform: translate(78px, -10px); */
    animation: move linear 8000ms;
    animation-direction: reverse;
    offset-path: path("M 102 -158 V -140.4 H 116.4 Q 121.2 -143.6 116.4 -138.8 H 82.8 Q 78 -134 82.8 -129.2 H 154.8 Q 159.6 -124.4 154.8 -119.6 H 44.4 Q 39.6 -114.8 44.4 -110 H 154.8 Q 159.6 -105.2 154.8 -100.4 H 44.4 Q 39.6 -95.6 44.4 -90.8 H 154.8 Q 159.6 -86 154.8 -81.2 H 42");
    offset-anchor: center;
    offset-rotate: 0deg;
}

@keyframes move {
  100% {
    offset-distance: 100%;
  }
}

.hotend:before {
    content: '';
    width: 30px;
    height: 30px;
    position: absolute;
    background-color: hsl(0, 0%, 35%);
    border: 2px dotted rgb(134, 134, 134);
    border-radius: 50%;
    transform: translate(5px, 5px);
}

.hotend:after {
    content: '';
    width: 30px;
    height: 20px;
    position: absolute;
    background-color:rgb(56, 56, 56);
    -webkit-clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
            clip-path: polygon(
        0% 20%, 15% 20%,
        15% 0%, 85% 0%,
        85% 25%, 100% 25%,
        100% 60%, 80% 65%,
        65% 90%, 35% 90%,
        20% 65% , 0% 60%);
    transform: translate(7.5px, 45px);
}

path {
  stroke-dasharray: 634.277;
  /* stroke-dashoffset: 634.277; */
  animation: draw linear 8000ms forwards;
  animation-direction: forwards;
}

@keyframes draw {
  from {
      stroke-dashoffset: 634.277;
  }
  to {
      stroke-dashoffset: 1268.554;
  }
}

